.corporate-content-section {
  &--one {
    position: relative;
    padding: 120px 0 60px;
    background-color: #eff3f1;

    .section-bg--left {
      width: 50%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: bottom left;
      }
    }
  }

  &--two {
    position: relative;
    padding: 60px 0 120px;
    background-color: #eff3f1;

    .section-bg--right {
      left: 0;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: right;
      }
    }

    .image {
      img {
        filter: drop-shadow(0 16px 32px rgba(12, 12, 13, 0.1));
      }
    }
  }

  &--three {
    position: relative;
    padding: 120px 0 0;
    background-color: #004520;

    .section-title {
      color: $white;
    }

    p {
      color: $white;
      max-width: 80%;
      margin: 0 auto;
    }
  }

  &--four {
    padding: 120px 0;
    overflow: hidden;

    .nav-tabs {
      border-bottom: 0;
      gap: 5px 20px;
      margin-bottom: 20px;

      > li {
        flex: 1;
      }

      .nav-link {
        border: 0;
        padding: 12px 20px;
        width: 100%;
        border-radius: 35px;
        color: #00423c;
        background-color: #eff3f1;
        font-size: 1.6rem;
        font-weight: 500;
        transition: all 0.2s ease-in;

        &.active,
        &:hover {
          background-color: #24ec7e;
          color: #09140e;
          isolation: inherit;
        }
      }
    }

    .main-content-wrapper {
      // background-color: #eff3f1;
      // border-radius: 32px;
      // overflow: hidden;

      [class*="col-"] {
        align-self: stretch;

        > * {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
        }
      }
    }

    .image {
      align-items: center;
      background: #004520;
      border-radius: 32px;
      // padding: 100px 30px 0;

      img {
        // max-height: 400px;
        width: 100%;
      }
    }

    .content {
      padding: $grid-gutter-width;
      background-color: #eff3f1;
      border-radius: 32px;

      h4 {
        font-size: 3.2rem;
      }

      .btn {
        min-width: 200px;
      }

      ul {
        display: grid;
        gap: 10px;
        list-style-type: none;
        margin: 20px 0 0;
        padding: 0;

        li {
          position: relative;
          padding-left: 35px;
          font-size: 1.8rem;

          &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 5px;
            width: 20px;
            height: 20px;
            background: url(../img/redesign/list-marker-green.svg) center center no-repeat;
            background-size: 20px;
          }
        }
      }
    }
  }

  &--five {
    padding: 120px 0;
    // background: url(../img/redesign/corporate/bg-shape.svg);
    // background-repeat: no-repeat;
    // background-position: top 32% center;
    // background-size: 100% auto;
    overflow: hidden;

    .sell--swiper {
      padding-bottom: 50px;
      overflow: visible;

      .swiper-slide {
        height: initial;
      }

      .swiper-pagination-bullet {
        width: 100px;
        height: 6px;
        border-radius: 2px;
        background-color: #eff3f1;
        opacity: 1;

        &.swiper-pagination-bullet-active {
          background-color: #373737;
        }
      }
    }
  }

  &--six {
    position: relative;
    background-color: #004520;
    overflow: hidden;

    .bg-img {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 80%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .section-title,
    p {
      color: $white;
    }

    .btn {
      min-width: 180px;

      &:hover {
        border-color: $white;
        color: $white;
      }
    }

    .main-content {
      padding: 120px 0;
    }

    .image {
      position: relative;
      right: 110px;

      img {
        // height: 500px;
        max-width: initial;
      }

      // .list {
      //   &--images {
      //     position: absolute;
      //     top: 0;
      //     bottom: 0;
      //     left: 0;
      //     right: 0;
      //     display: flex;
      //     flex-direction: column;
      //     justify-content: flex-end;
      //     margin-bottom: 20px;
      //     gap: 20px;
      //   }

      //   &--image {
      //     margin-left: -50px;

      //     &:last-child {
      //       margin-right: -50px;
      //       margin-left: auto;
      //     }

      //     img {
      //       border-radius: 8px;
      //       box-shadow: 0px 143.494px 40.045px 0px rgba(0, 0, 0, 0), 0px 91.213px 36.708px 0px rgba(0, 0, 0, 0.01),
      //         0px 51.169px 31.146px 0px rgba(0, 0, 0, 0.05), 0px 23.36px 23.36px 0px rgba(0, 0, 0, 0.09),
      //         0px 5.562px 12.236px 0px rgba(0, 0, 0, 0.1);
      //     }
      //   }
      // }
    }
  }

  &--seven {
    position: relative;
    padding: 120px 0;

    p {
      max-width: 85%;
      margin: 0 auto;
    }

    .content {
      &-wrapper {
        position: relative;
        z-index: 1;
      }

      &-img {
        margin-top: 100px;
        text-align: center;

        &--items {
          margin-top: 100px;
          display: flex;
          justify-content: center;
          gap: 50px;

          // >* {
          //   flex: 1;
          // }
        }

        &--item {
          text-align: center;

          &:nth-child(2) {
            margin-top: 50px;
          }
        }
      }
    }
  }

  &--eight,
  &--thirteen {
    padding: 120px 0;
    overflow: hidden;

    .title-row {
      p {
        max-width: 70%;
        margin: 0 auto;
      }
    }
  }

  &--nine {
    padding: 60px 0;

    .payment-list {
      &--items {
        display: grid;
        gap: 15px;
        margin: 25px 0 30px;
      }

      &--item {
        position: relative;
        padding-left: 35px;

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 5px;
          width: 20px;
          height: 20px;
          background: url(../img/redesign/list-marker-green.svg) center center no-repeat;
          background-size: 20px;
        }

        h4 {
          font-size: 2rem;
          font-weight: 600;
          margin-bottom: 2px;
        }

        p {
          font-size: 1.6rem;
          line-height: 1.2;
          margin: 0;
        }
      }
    }
  }

  &--ten {
    padding: 120px 0;
  }

  &--eleven {
    background-color: $content-link;
    padding: 120px 0;

    .title-row {
      .section-title {
        color: $white;
        line-height: 1.2;
      }

      p {
        color: #ebf0ed;
        max-width: 70%;
        margin: 0 auto;
      }
    }

    .payment-acceleration {
      &--items {
        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 30px;
      }

      &--item {
        background-color: rgba(#168e4c, 0.2);
        box-shadow: 0px 14px 48px 0px rgba($black, 0.05);
        border-radius: 8px;
        padding: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: 0.3s ease;

        .content {
          &-text {
            flex-grow: 1;

            h4 {
              color: $white;
              margin-bottom: 10px;
            }

            p {
              color: $white;
              font-size: 2rem;
              line-height: 1.35;
              margin-bottom: 30px;
            }
          }

          &-cta {
            display: flex;
            align-items: center;
            gap: 20px;

            .btn {
              border-radius: 4px;
              font-size: 1.2rem;
              font-weight: 400;

              &:not(.btn-link) {
                padding: 5px 10px;
              }
            }
          }
        }

        &:hover {
          background-color: #053a1e;
          box-shadow: rgba(4, 58, 30, 1) 0px 8px 32px 0px;
          transform: translateY(-4px);
        }
      }
    }
  }

  &--twelve {
    padding: 120px 0;

    .option-list {
      &--items {
        display: grid;
        gap: 15px;
        margin: 25px 0 30px;
      }

      &--item {
        position: relative;
        padding-left: 35px;

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 5px;
          width: 20px;
          height: 20px;
          background: url(../img/redesign/list-marker-green.svg) center center no-repeat;
          background-size: 20px;
        }

        h4 {
          font-size: 2rem;
          font-weight: 500;
          margin-bottom: 2px;
        }

        p {
          font-size: 1.6rem;
          line-height: 1.2;
          margin: 0;
        }
      }
    }
  }

  &--fourteen {
    position: relative;
    padding: 120px 0;
    background-color: #004520;
    color: $white;
    overflow: hidden;

    .title-row {
      p,
      .section-title {
        color: $white;
      }
    }

    .image {
      position: absolute;
      right: 50px;
      top: -80px;
      bottom: -80px;
      width: 650px;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    .btn-bright-green {
      &:hover {
        border-color: $white;
        color: $white;
      }
    }
  }
}

.partner--swiper {
  position: relative;
  overflow: visible;

  .swiper-slide {
    width: auto;
  }

  .swiper-wrapper {
    transition-timing-function: linear;
  }

  .image {
    padding: 20px;
    width: 175px;
    height: 100px;
    background-color: $white;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 12px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

// < 1600px
@include media-breakpoint-down(xxl) {
  .corporate-content-section {
    &--six {
      .image img {
        max-width: 850px;
      }
    }

    &--fourteen .image {
      width: 43%;
    }
  }
}

// < 1400px
@media (max-width: 1400px) {
  .corporate-content-section {
    &--six {
      .image img {
        max-width: 720px;
      }
    }
  }
}

