// < 1200px
@include media-breakpoint-down(xl) {
  .corporate-content-section {
    &--six {
      .main-content {
        text-align: center;
        padding: 100px 0;

        p {
          max-width: 80%;
          margin: 0 auto;
        }
      }

      .image {
        right: 0;
        //   position: relative;
        //   left: 0;
        //   right: 0;
        //   width: 80%;
        //   margin: 0 auto;

        img {
          max-width: 100%;
        }
      }
    }

    &--seven {
      .content-img {
        margin-top: 50px;
      }
    }

    &--eight,
    &--thirteen {
      padding-top: 0;
    }

    &--eleven {
      .payment-acceleration {
        &--items {
          grid-template-columns: repeat(2, minmax(0, 1fr));
        }
      }
    }

    &--fourteen .image {
      right: 0;
    }
  }
}

// < 992px
@include media-breakpoint-down(lg) {
  .corporate-content-section {
    &--one {
      .section-bg--left {
        width: 100%;

        img {
          object-fit: contain;
        }
      }
    }

    &--four {
      padding-bottom: 0;

      .main-content-wrapper {
        background-color: #eff3f1;
      }

      .content {
        // padding: $grid-gutter-width $grid-gutter-width/2;
      }

      .main-content-wrapper,
      .image {
        border-radius: 12px;
      }
    }

    &--nine {
      .payment-list {
        &--items {
          margin: 30px 0;
        }
      }
    }

    &--twelve {
      .option-list {
        &--items {
          margin: 20px 0 30px;
        }
      }
    }

    &--fourteen .image {
      position: relative;
      top: 0;
      bottom: 0;
      width: 60%;
      margin: 50px auto 0;
    }
  }
}

// < 768px
@include media-breakpoint-down(md) {
  .corporate-content-section {
    &--five,
    &--seven,
    &--ten,
    &--eleven,
    &--twelve {
      padding: 50px 0;
    }

    &--one {
      padding: 50px 0 25px;
    }

    &--two {
      padding: 25px 0 50px;
    }

    &--three,
    &--four {
      padding-top: 50px;
    }

    &--eight,
    &--thirteen {
      padding-bottom: 50px;
    }

    &--three,
    &--six,
    &--seven,
    &--eight,
    &--eleven,
    &--thirteen {
      p,
      .title-row p {
        max-width: 100%;
      }
    }

    &--four {
      .nav-tabs {
        flex-direction: column;

        .nav-link {
          width: 100%;
        }
      }

      .image {
        padding: 50px 20px 0;
      }

      .content {
        padding: $grid-gutter-width/2;

        h4 {
          font-size: 2.4rem;
        }

        ul li {
          font-size: 1.6rem;
        }
      }
    }

    &--five {
      .sell--swiper .swiper-pagination-bullet {
        width: 30px;
        height: 4px;
      }

      .w-50 {
        width: 100% !important;
      }
    }

    &--six {
      .main-content {
        padding: 50px 0;
      }

      // .image {
      //   width: 95%;
      // }
    }

    &--seven {
      .content-img {
        margin-top: 0;

        &--items {
          gap: 15px;
          margin-top: 0;
          flex-direction: column;
        }

        &--item {
          &:nth-child(2) {
            margin-top: 0;
          }

          img {
            max-height: 300px;
          }
        }
      }
    }

    &--nine,
    &--twelve {
      .payment-list,
      .option-list {
        &--item {
          h4 {
            font-size: 1.6rem;
          }
        }
      }
    }

    &--eleven {
      .payment-acceleration {
        &--items {
          grid-template-columns: auto;
          gap: $grid-gutter-width/2;
        }
        &--item .content-text p {
          font-size: 1.6rem;
        }
      }
    }

    &--fourteen {
      padding: 50px 0;

      .image {
        width: 100%;
        max-width: 500px;
        margin: 20px auto 0;
      }
    }
  }

  .partner--swiper {
    .image {
      padding: 10px;
      width: 150px;
      height: 80px;
    }
  }
}

// < 576px
@include media-breakpoint-down(sm) {
  .corporate-content-section--six .image .list {
    &--images {
      gap: 5px;
    }

    &--image {
      margin-left: -20px;
      max-width: 150px;

      &:last-child {
        margin-right: -20px;
      }
    }
  }
}
